import React, { useState } from "react"
import { FDialog } from "#/notice/dialog/FDialog"
import { FButton } from "#/base/button/FButton"
import { toast } from "@/api/toast"
import { febDialog } from "@/api/dialog"
import { FIconUser } from "+/FIconUser"
import { FIconSuccessFilled } from "+/FIconSuccessFilled"

// 
export const FDialogTest = function () {
  const [visible, setVisible] = useState(false)
  return (<div>
    <FButton variant="outline" type="primary" onClick={() => {
      setVisible(true)
    }}></FButton>
    <FDialog
      onConfirm={() => {
        toast.success('')
        setVisible(false)
      }}
      onCancel={() => {
        toast.fail('')
        setVisible(false)
      }}
      onClose={() => {
        toast('')
        setVisible(false)
      }}
      onClickModal={() => {
        toast('')
        setVisible(false)
      }}
      title=""
      visible={visible}
      content="">
    </FDialog>
  </div>)
}

// 
export const HeaderTest = function () {
  const [visible, setVisible] = useState(false)
  return (<div>
    <FButton variant="outline" type="primary" onClick={() => {
      setVisible(true)
    }}></FButton>
    <FDialog
      onClickModal={() => {
        setVisible(false)
      }}
      header={
        <div style={{
          alignItems: 'center',
          display: 'flex',
          width: "100%",
          height: '40px',
          justifyContent: 'center',
          fontSize: 20
        }}>
          FEB-UI
        </div>
      }
      visible={visible}
      content={(function () {
        let a = new Array(100)
        for (let i = 0; i < a.length; i++) {
          a[i] = ''
        }
        return a.join('')
      })()}>
    </FDialog>
  </div>)
}

// 
export const FooterTest = function () {
  const [visible, setVisible] = useState(false)
  const select = (ans: string) => {
    if (ans === 'A') {
      toast.success('')
    } else {
      toast.fail('')
    }
    setVisible(false)
  }
  return (<div>
    <FButton variant="outline" type="primary" onClick={() => {
      setVisible(true)
    }}></FButton>
    <FDialog
      title=" C "
      onClickModal={() => {
        setVisible(false)
      }}
      footer={
        <div style={{
          alignItems: 'center',
          display: 'flex',
          width: "100%",
          height: '40px',
        }}>
          
          <FButton onClick={() => select('A')} type="primary" variant="text" shape="circle" >A</FButton>
          <FButton onClick={() => select('B')} type="primary" variant="text" shape="circle" >B</FButton>
          <FButton onClick={() => select('C')} type="primary" variant="text" shape="circle" >C</FButton>
          <FButton onClick={() => select('D')} type="primary" variant="text" shape="circle" >D</FButton>
        </div>
      }
      visible={visible}>
      <div style={{ lineHeight: 2 }}>
        <div>A ++i;</div>
        <div>B a=b=34</div>
        <div>C a=3,b=9</div>
        <div>D k=int(a+b);</div>
      </div>
    </FDialog>
  </div>)
}

// API
export const ApiTest = function () {
  return (<div style={{display: 'flex', gap: 10, flexWrap: 'wrap'}}>
    <FButton variant="outline" type="primary" onClick={() => {
      febDialog({
        title: '',
        content: '',
      }).then(() => {
        toast('')
      })
    }}></FButton>
    <FButton variant="outline" type="primary" onClick={() => {
      febDialog({
        title: '',
        content: '',
        showClose: true
      }).then((res) => {
        toast(res ? '' : '')
      })
    }}></FButton>
     <FButton variant="outline" type="primary" onClick={() => {
      febDialog({
        title: '',
        content: '',
        showCancel: true
      }).then((res) => {
        toast(res ? '' : '')
      })
    }}></FButton>
    <FButton variant="outline" type="primary" onClick={() => {
      febDialog({
        title: '',
        content: '',
        showCancel: true,
        throwReject: true
      }).then(() => {
        toast('')
      }).catch(() => {
        toast.fail('')
      })
    }}></FButton>
    <FButton variant="outline" type="primary" onClick={() => {
      febDialog({
        title: '',
        content: '',
        showCancel: true,
        confirmText: '',
        cancelText: ''
      })
    }}></FButton>
    <FButton variant="outline" type="primary" onClick={() => {
      febDialog({
        title: '',
        content: '',
        showCancel: true,
        confirmType: 'danger',
        cancelType: 'safe',
        confirmVariant: 'outline',
      })
    }}></FButton>
    <FButton variant="outline" type="primary" onClick={() => {
      febDialog({
        title: '',
        titleIcon: FIconUser,
        titleIconColor: 'var(--primary-color-8)',
        content: '',
        showCancel: true,
        confirmText: '',
        cancelText: ''
      })
    }}></FButton>
  </div>)
}

