// 
export const DivTest = function () {
  return (
    <FLayout style={{ height: 200 }}>
      <FLayoutHead showDiv>
        
      </FLayoutHead>
      <FLayoutAside showDiv>
        
      </FLayoutAside>
      <FLayoutContent></FLayoutContent>
      <FLayoutFooter showDiv></FLayoutFooter>
    </FLayout>
  )
}

