import { FTab } from "#/nav/tab/FTab"
import { FTabOptionValue } from "@/types/tab"
import React, { useState } from "react"

// 
export const FTabTest1 = function () {
      const [val, setVal] = useState<FTabOptionValue>('2')
      return (<div>
            <FTab value={val} onChange={(v) => {
                  setVal(v)
            }} options={[
                  {
                        value: '1',
                        label: '----1'
                  }, {
                        value: '2',
                        label: '2'
                  }, {
                        value: '3',
                        label: '3'
                  },
            ]} />
      </div>)
}

