// 
export const CollapseTest = function () {

  let status = false
  const [collapsed, setCollapsed] = useState(status)
  const [current, setCurrent] = useState('password')
  const items: FMenuItem[] = [
    {
      name: 'account',
      icon: FIconUser,
      label: '',
      children: [
        {
          name: 'balance',
          icon: FIconBankCard,
          label: ''
        }, {
          name: 'safety',
          icon: FIconSafety,
          label: '',
          children: [
            {
              name: 'password',
              label: ''
            }, {
              name: 'access',
              label: ''
            }, {
              name: 'device',
              label: ''
            }
          ]
        }
      ]
    },
    {
      name: 'system',
      icon: FIconSystem,
      label: '',
      children: [
        {
          name: 'dashboard',
          label: '',
          icon: FIconDataSheet
        }, {
          name: 'data',
          label: '',
          icon: FIconData
        }, {
          name: 'visual',
          label: '',
          icon: FIconChartLine
        }
      ]
    },
    {
      name: 'setting',
      icon: FIconSettingOne,
      label: '',
      children: [
        {
          name: 'reuse',
          icon: FIconDeleteFour,
          label: ''
        }
      ]
    }
  ]
  const onClick = () => {
    setCollapsed(status = !status)
  }
  return (
    <div>
      <div style={{ height: 30 }}>{current}</div>
      <FMenu collapsed={collapsed} onChange={setCurrent} default={current} style={{ width: 200 }} items={items}></FMenu>
      <FButton variant="text" type="primary" onClick={onClick}></FButton>
    </div>
  )
}